export default {
	data() {
		return {
			title: 'radio',
			items: [{
					value: 'USA',
					name: '经典蓝',
					checked: 'true'
				},
				{
					value: 'CHN',
					name: '魅力红'
				},
				{
					value: 'BRA',
					name: '活力蓝'
				},
				{
					value: 'JPN',
					name: '孔雀绿'
				}
			],
			current: 0
		}
	},
	methods: {
		radioChange(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		}
	}
}
